/*Proceso*/
@use "base/variables" as v;
@use "base/mixins" as m;

.proceso-cafe {
    margin-top: calc(v.$separacion / 2);

    @include m.tablet {
        display: flex;
        align-items: center;
        gap: v.$separacion;
        -webkit-column-gap: v.$separacion;
    }

    &:nth-child(even) {
        .texto {
            order: -1;
        }
    }    
    
    .imagen {
        flex: 1;
    }
    
    .texto {
        flex: 1;
        margin: 1rem  0 2rem 0;
        
        h3 {
            text-align: left;
            margin: 0;
            font-size: 2.5rem;
            color: v.$primario;

            &::after {
                content: '';
                display: block;
                width: 50%;
                height: .5rem;
                background-color: v.$secundario;
            }
        }
    }
}