/*NOSOTROS*/

@use "base/variables" as v;
@use "base/mixins" as m;

.contenido-nosotros {

    @include m.tablet {
         @include m.grid(2, 2rem)

    }
    
    .imagen {
        margin: 0 0 5rem 0;
        transform: rotate(-6deg);
        
        @include m.tablet {
        margin: 0 1rem 0 0;
            
        }
        img {
            border: 3rem solid v.$blanco;
            box-shadow: 9px 10px 5px -7px rgb(0 0 0 / .35);
            
        }
    }

    .texto {
        blockquote {
            margin: 0;
            padding: 1rem;
            border-left: .5rem solid v.$primario;
            font-size: 2rem;
            line-height: 1.5;
        }
    }

    }
