@use "base/variables" as v;
@use "base/mixins" as m;

.contenido-contacto {

    @include m.tablet {
        @include m.grid(2, v.$separacion);
        background-image: url(../img/granos_cafe.jpg);
        background-repeat: no-repeat;
        background-position: right center;
        background-size: 40%;
    }

    .formulario {

        .campo {
            margin-bottom: calc(v.$separacion / 2);
            &:last-of-type {
                margin-bottom: 0;
            }
            
            label {
                display: block;
                font-size: 2.2rem;
                text-transform: uppercase;
                font-weight: 700;
            }

            input:not(input[type="submit"]),
            select {
                width: 100%;
                padding: 1rem;
                border: .3rem solid v.$primario;
            }

            input[type="submit"] {
                width: 100%;
                background-color: v.$primario;
                padding: 2rem;
                color: v.$blanco;
                font-size: 2.2rem;
                border: none;


            }
        }


    }
}