@use 'variables' as v;
@use 'mixins' as m; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
 *::before,
  *::after {
    box-sizing: inherit;
}

body {
    font-family: v.$fuenteSecundaria;
    font-size: 1.6rem;
    background-color: v.$cafeClaro;
}

p {
    color: v.$negro;
    font-size: 1.6rem;
    line-height: 2;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
    @include m.desktop {
    width: 90%;
    max-width: 180rem;
        
    }
}

a {
    text-decoration: none;
    color: #000000;
    display: inline-block;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}