@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding: 10rem 0;
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    
    @include m.telefono {
        padding: 5rem;
    }
    
    &::before {
        content: "";
        background-color: v.$negro;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: .6;
    }
    .contenedo-header {
        position: relative;
        min-height: 40rem;

        h1 {
            color: v.$blanco;
            margin-top: 1rem;
            padding: 0 .5rem;
            text-align: center;
            text-transform: uppercase;
            font-size: 3rem;
            font-weight:  900;
            line-height: 1.3;
            @include m.desktop {
                margin-top: 10rem;
            }   
        }
    }
}

//HEADER del inicio
.header-inicio {

    .notavif.notwebp & {
        background-image: url(../img/header_inicio.jpg);
    }

    .webp & {
        background-image: url(../img/header_inicio.webp);
    }

    .avif & {
        background-image: url(../img/header_inicio.avif);
    }
}

