@use 'base/variables' as v;
@use 'base/mixins' as m;

.nav-principal {
    display: flex;
    flex-direction: column;
    margin-top: 4rem;
    margin-bottom: 3rem;
    @include m.tablet {
        flex-direction: row;
        align-items: flex-start;
    }
    a {
        color: v.$blanco;
        text-align: center;
        font-size: 1.8rem;
        margin: 1rem;
        font-weight: 700;
        text-transform: uppercase;
        @include m.tablet {
        &:last-of-type {
            margin: 0 1rem 0 0;
        }
            font-size: 1.4rem;
            margin: 0 1rem 0 0 ;
        }
        @include m.desktop {
            font-size: 2rem;
        
        &:hover {
            color: v.$secundario;
        }
        }
        &.activo {
            color: v.$secundario;
        }
    }
}