@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$primario;
    padding-top: 5rem;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.185);
    
    .contenedor-footer {
        background-color: v.$blanco;
        padding: 5rem 2rem;

        margin-top: -5rem;
        box-shadow: 0px 10px 10px 2px rgb(0 0 0 / .2);

        @include m.tablet {
            @include m.grid(3, 2rem);
        }

        .footer-box {
            margin-bottom: 5rem;
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: baseline;


            @include m.tablet {
                margin-bottom: 0;
            }
            h3 {
                font-family: v.$fuenteCursiva;
                color: v.$secundario;
                margin-bottom: .5rem;
                line-height: 1.1;
                font-size: 2.6rem;
                text-align: center;
                @include m.tablet {
                    font-size: 2.2rem;
                    margin-bottom: 1rem;

                }
            }
            p {
                font-family: v.$fuenteCursiva;
                margin: 0;
                line-height: 2;
                font-size: 2rem;
                @include m.tablet {
                    font-size: 1.8rem;
                    
                }
            a{
                font-family: v.$fuenteCursiva;
            }

            
            }
    
        }
        .horarios {
            margin-bottom: 0;
        }
    }
    .copyright {
        margin: 0;
        padding: 2rem;
        font-size: 2rem;
        font-family: v.$fuenteCursiva;
        text-align: center;
        color: v.$blanco;

    }
                
    .tel {
        background-color: v.$primario;
        color: v.$blanco;
        display: inline-block;
        padding: .5rem 2rem;
        margin: 0 auto;
        text-align: center;
        font-size: 2.2rem;
        font-weight: 700;
        line-height: 1;
        border-radius: 1rem;
        transition-property: background-color;
        transition-duration: 300ms;
        border: 1px solid v.$primario;

        &:hover {
            background-color: v.$blanco;
            color: v.$negro;
            transform: scale(.8);
        }
    }
}