@use 'base/variables' as v;
@use 'base/mixins' as m;

.iconos {
    margin-top: 7rem;

    .listado-iconos {
        @include m.tablet {
            @include m.grid(3, 3rem);
        }
        
        li {
            margin-bottom: 2rem;
            img {
                margin: 0 auto;
                width: 12rem;
            }
            
            p {
                font-family: v.$fuenteCursiva;
                font-size: 3.6rem;
                line-height: 1.4;
                margin-top: 0;
            }
        }
    }
}