@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-principal {
    padding: 5rem 2rem;
    background-color: v.$blanco;
    margin-top: -9rem;
    position: relative;

    @include m.telefono {
        padding: 4rem;
    }

    @include m.tablet {
        padding: 10rem;
    }

    h2 {
        color: v.$primario;
        line-height: 1;
        span {
            color: v.$secundario;
            font-size: 1.8rem;
            display: block;
        }
        &::after {
            display: block;
            margin: 0 auto;
            content: '';
            width: 10rem;
            height: 10rem;
            background-image: url(../img/cafe.svg);
            background-size: 10rem;
        }
    }
}