@use "base/variables" as v;
@use "base/mixins" as m;

.galeria {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;

  @include m.telefono {
    @include m.grid(2, 1rem);
  }

  @include m.desktop {
    @include m.grid(4, 1rem);
  }

  img {
    max-width: 100%;
    height: 30rem;
    object-fit: cover;
    margin: 0 auto;

    @include m.telefono {
      width: 30rem;
    }
  }

  .imagen {
    margin-bottom: 2rem;
    width: 30rem;

    @include m.telefono {
      margin-bottom: 0;
      width: 100%;
    }

    @include m.desktop {
      &:nth-child(1) {
        grid-row: 1 / 3;

        img {
          height: 62rem;
        }
      }
      &:nth-child(2) {
        grid-column: 2 / 4;

        img {
          width: 62rem;
        }
      }

      &:nth-child(4) {
        grid-row: 2/ 4;

        img {
          height: 62rem;
        }
      }
      &:nth-child(5) {
        grid-column: 3 / 5;

        img {
          width: 62rem;
        }
      }
      &:nth-child(6) {
        grid-column: 3 / 5;

        img {
          width: 62rem;
        }
      }
      &:nth-child(7) {
        grid-row: 3 / 5;

        img {
          height: 62rem;
        }
      }
      &:nth-child(8) {
        grid-column: 2 / 5;

        img {
          width: 94rem;
        }
      }
      &:nth-child(9) {
        grid-column: 1 / 3;

        img {
          width: 62rem;
        }
      }
      &:nth-child(10) {
        grid-column: 3 / 5;

        img {
          width: 62rem;
        }
      }

      .imagen-descripcion {
        p {
          margin-top: -30rem;
        }
      }
    }
  }
}
