@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonial {
    padding: 10rem 0;
    margin-top: -8rem;
    background-color: v.$blanco;
    border-top-right-radius: 1rem;
    border-top-left-radius: 1rem;

    .testimoniales {
        background-color: v.$cafeClaro;
        max-width: 90%;

        display: flex;
        flex-direction: column;
        justify-content: space-between;

        margin: 0 auto;
        padding: 2rem 3.3rem;
        border-radius: 1rem;

        @include m.tablet {
            max-width: 70rem;
            flex-direction: row;

        }

        @include m.desktop {
            padding: 3rem 8rem;
        }

        .testimoniales-texto {
            flex-basis: 70%;

            @include m.tablet {
                padding-right: 4rem;
            }
        }

        .testimoniales-autor {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 50%;
            max-width: 15rem;
            margin: 0 auto;

        }

        img {
            width: 30rem;
            border-radius: 10rem;
        }

        .autor {
            color: v.$primario;
            font-size: 1.6rem;
            font-weight: 700;
            text-align: center;
            margin: 0;
            font-family: v.$fuenteCursiva;
        }
    }
}