@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-menu {

    background-image: url(../img/cafe.jpg);
    background-position: center bottom;
    background-size: 100%;
    background-repeat: no-repeat;
    aspect-ratio: 1 / 2.4;
    
    @media(min-width: 400px) {
        aspect-ratio: 1 / 2.1;

    }
    @media(min-width: 440px) {
        aspect-ratio: 1 / 2;

    }
    
    @include m.telefono {
        aspect-ratio: 1 / 2;
    }
    
    @media(min-width: 550px) {
        aspect-ratio:  1 / 1.3;
        background-size: 60%;

    }
    @media(min-width: 608px) {
        aspect-ratio:  1 / 1.1;
        background-size: 60%;

    }
    @include m.tablet {
        @include m.grid(2, calc(v.$separacion / 2));
        background-size: 50%;
        background-position: right bottom;
        aspect-ratio:  unset;
        
        
    }

    @include m.desktop {
        background-size: 30%;
    }
}