@use 'variables' as v;

h1,
h2,
h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 v.$separacion 0;
    text-align: center;
    text-transform: uppercase;
    line-height: 1.5;
}

h1 {
    font-size: 4rem;
}

h2 {
    color: v.$primario;
    line-height: 1;
    span {
        color: v.$secundario;
        font-size: 1.8rem;
        display: block;
    }
    &::after {
        display: block;
        margin: 0 auto;
        content: '';
        width: 10rem;
        height: 10rem;
        background-image: url(../img/cafe.svg);
        background-size: 10rem;
    }
    &.heading-blanco {
        color: v.$blanco;
        span {
            color: v.$blanco;
        }
        &::after {
            display: none;
        }
    }
}

h3 {
    font-size: 3.2rem;
}