@use 'base/variables' as v;
@use 'base/mixins' as m;

.nuestro-menu {
    padding: 15rem 0 25rem 0;
    background-repeat: no-repeat, no-repeat;
    background-position: center 4rem, center 92%, 100%;
    background-size: 80%, 80%, 100%;
    background-image: url(../img/menu_arriba.svg),
        url(../img/menu_abajo.svg),
        linear-gradient(to top, v.$primario, v.$primario);

    @include m.tablet {
        background-size: 70%, 70%, 100%;
        padding: 15rem 5rem 25rem 5rem;
    }

    @include m.desktop {
        background-size: 50%, 50%, 100%;
    }
}

.grid-menu {
    @include m.tablet {
        @include m.grid(2, 4rem);

    }

    .menu {
        h3 {
            color: v.$blanco;
        }

        &.comida {
            margin-top: 5rem;
            @include m.tablet {
                margin-top: 0;
            }
        }
        
        li {
            display: flex;
            justify-content: space-between;
            align-items: center;

            p {
                line-height: 1.3;
                color: v.$blanco;
                font-size: 2.2rem;

                span {
                    display: block;
                    color: v.$secundario;
                    font-size: 2rem;
                }
            }

            .precio {
                font-size: 3rem;
            }
        }
    }
}